sor: -webkit-grabbing;\n}\n@mixin noSelect() {\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -khtml-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\n@mixin appBackground() {\n  background-color: $pink;\n  animation: hueRotate infinite 40s;\n}\n"],"sourceRoot":""}]);
